import React, {Component} from "react";
import {NavBar, WingBlank, List, InputItem, TextareaItem, Button} from "antd-mobile";
import {connect} from "react-redux";
import {Redirect} from "react-router-dom";

import HeaderSelector from '../../components/headerSelector/headerSelect';
import {updateUser} from "../../redux/actions";

class LaobanInfo extends Component {
  state = {
    header: '',
    info: '',
    post: '',
    salary: '',
    company: ''
  };
  setHeader = (header) => {
    this.setState({header});
  };
  handelChange = (name, value) => {
    this.setState({
      [name]: value
    })
  };
  save = () => {
    this.props.updateUser(this.state);
  };

  render() {
    const {header} = this.props.user;
    if (header) {
      return <Redirect to='/laoban'/>
    }
    return (
      <div>
        <NavBar>老板信息完善</NavBar>
        <HeaderSelector setHeader={this.setHeader}/>
        <WingBlank>
          <List>
            <InputItem placeholder='请输入招聘职位' onChange={val => this.handelChange('post', val)}>招聘职位</InputItem>
            <InputItem placeholder='请输入公司名称' onChange={val => this.handelChange('company', val)}>公司名称</InputItem>
            <InputItem placeholder='请输入职位薪资' onChange={val => this.handelChange('salary', val)}>职位薪资</InputItem>
            <TextareaItem title='职位要求' rows={3} onChange={val => this.handelChange('info', val)}/>
            <Button type='primary' onClick={this.save}>保存</Button>
          </List>
        </WingBlank>
      </div>
    )
  }
}

export default connect(
  state => ({user: state.user}),
  {updateUser}
)(LaobanInfo);


